@use '../../styles.scss';

.todoLists {
  display: flex;
  gap: 20px;
  padding: 8px;

  @media only screen and (max-width: 478px) {
    gap: 10px;
  }
}

.todo {
  font-family: styles.$font-family;
  font-size: styles.$font-size;
  letter-spacing: styles.$letter-spacing;
  line-height: 2;
  padding: 0 8px;

  border-bottom: 1px solid styles.$secondary-color;
  overflow: hidden;
  white-space: nowrap;
  flex: 2;

  @media only screen and (max-width: 468px) {
    font-size: styles.$font-size-small;
  }
}

.completed {
  background-color: rgb(156, 252, 156);
  border: none;
  border-radius: 8px;
  opacity: 0.7;
}

.buttonsWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  gap: 16px;
  flex: 1;

  @media only screen and (max-width: 468px) {
    width: 120px;
    gap: 8px;
  }
}
